import { Pipe, PipeTransform } from '@angular/core';
import { Book } from './models/book.model';
import { BookFilter } from './models/bookFilter.model';

@Pipe({
  name: 'bookFilter',
  pure: false
})
export class BookFilterPipe implements PipeTransform {

  transform(books: Book[], filters: BookFilter[]){
    if(filters.length === 0 || !books){
      return books;
    }

    let filteredBooks: Book[] = [];
    filters.forEach((filterItem, index) => {
      if (index === 0){
        filteredBooks = books;
      }

      filteredBooks = filteredBooks.filter(book => filterItem.filter(book))
    });

    return filteredBooks;
  }

}
