import { Component, OnInit, ViewChild, ElementRef, Output, EventEmitter } from '@angular/core';
import { BooksService } from '../services/books.service';
import { BookFilter } from '../models/bookFilter.model';

@Component({
  selector: 'app-filterbar',
  templateUrl: './filterbar.component.html',
  styleUrls: ['./filterbar.component.css']
})
export class FilterbarComponent implements OnInit {

  @ViewChild('availableFilterBtn', {static: true}) availableFilterBtn: ElementRef<HTMLButtonElement>;
  @ViewChild('unavailableFilterBtn', {static: true}) unavailableFilterBtn: ElementRef<HTMLButtonElement>;
  @ViewChild('noAvailabilitiesFilterBtn', {static: true}) noAvailabilitiesFilterBtn: ElementRef<HTMLButtonElement>;
  @ViewChild('notFoundFilterBtn', {static: true}) notFoundFilterBtn: ElementRef<HTMLButtonElement>;
  @ViewChild('allFilterBtn', {static: true}) allFilterBtn: ElementRef<HTMLButtonElement>;
  @ViewChild('ebooksFilterBtn', {static: true}) ebooksFilterBtn: ElementRef<HTMLButtonElement>;

  @Output() bookFiltersChanged: EventEmitter<BookFilter[]> = new EventEmitter<BookFilter[]>();

  public search: string;
  private activeFilterBtn: ElementRef<HTMLButtonElement>;
  private filters: BookFilter[] = [];

  constructor() { }

  ngOnInit() {
    this.onAvailableFilterBtnClick();
  }

  onAvailableFilterBtnClick(){
    this.toggleButton(this.availableFilterBtn, 'AVAILABLE');
  }

  onUnavailableFilterBtnClick(){
    this.toggleButton(this.unavailableFilterBtn, 'UNAVAILABLE');
  }

  onNoAvailabilitiesBtnClick(){
    this.toggleButton(this.noAvailabilitiesFilterBtn, 'NO_AVAILABILITIES');
  }

  onNotFoundBtnClick(){
    this.toggleButton(this.notFoundFilterBtn, 'NOT_FOUND');
  }

  onAllFilterBtnClick(){
    this.toggleButton(this.allFilterBtn, 'ALL');
    this.removeFilter('status');
    this.removeFilter('ebooks');
    this.addFilter({
      name: 'status',
      filter: (book => 1 === 1)
    });
  }

  onEbooksFilterBtnClick(){
    this.toggleButton(this.ebooksFilterBtn, 'EBOOKS');
    this.removeFilter('status');
    this.addFilter({
      name: 'ebooks',
      filter: (book => !!book.cloudlibrary_id)
    })
  }

  private removeFilter(filterName: string){
    this.filters = this.filters.filter(x => x.name !== filterName);
    this.bookFiltersChanged.emit(this.filters);
  }

  private addFilter(filter: BookFilter){
    this.filters.push(filter);
    this.bookFiltersChanged.emit(this.filters);
  }

  private toggleButton(button: ElementRef<HTMLButtonElement>, status: string){
    this.removeFilter('status');
    this.removeFilter('ebooks')

    if(this.activeFilterBtn === button){
      this.setButtonInactive(button);
      this.activeFilterBtn = null;
    } else {
      this.addFilter({
        name: 'status',
        filter: (book => book.status === status)
      });
      if(this.activeFilterBtn){
        this.setButtonInactive(this.activeFilterBtn);
      }
      
      this.setButtonActive(button);
    }
  }

  private setButtonInactive(button: ElementRef<HTMLButtonElement>){
    button.nativeElement.classList.remove('btn-secondary');
    button.nativeElement.classList.add('btn-outline-secondary');
  }

  private setButtonActive(button: ElementRef<HTMLButtonElement>){
    button.nativeElement.classList.remove('btn-outline-secondary');
    button.nativeElement.classList.add('btn-secondary');
    this.activeFilterBtn = button;
  }

  onSearchInputChange(element: HTMLInputElement){
    if(this.activeFilterBtn !== this.allFilterBtn){
      this.setButtonInactive(this.activeFilterBtn)
      this.setButtonActive(this.allFilterBtn)
    }
    this.setSearchFilter(this.search);
  }

  private setSearchFilter(query: string){
    this.removeFilter('search');
    this.addFilter({
      name: 'search',
      filter: (book => book.title.toLowerCase().includes(query.toLowerCase()) || book.author.toLowerCase().includes(query.toLowerCase()))
    });
  }

}
