import { Component, OnInit, Input } from '@angular/core';
import { Availability } from '../models/availability.model';

@Component({
  selector: 'app-availabilities',
  templateUrl: './availabilities.component.html',
  styleUrls: ['./availabilities.component.css']
})
export class AvailabilitiesComponent implements OnInit {

  @Input() public availabilities: Availability[];
  @Input() public bookStatus: string;
  
  public today: Date;

  constructor() { }

  ngOnInit() {
    if(this.bookStatus === 'AVAILABLE'){
      this.availabilities = this.availabilities.filter(x => x.is_available);
    }

    this.today = new Date();
  }

}
