import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Book } from '../models/book.model.js';
import { Availability } from '../models/availability.model.js';
import { map } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { Library } from '../models/library.model.js';
import { AvailabilitiesComponent } from '../availabilities/availabilities.component.js';
import { format } from 'url';

@Injectable()
export class BooksService {

    public books: Subject<Book[]> = new Subject<Book[]>();
    public libraries: Subject<Library[]> = new Subject<Library[]>();

    constructor(private http: HttpClient){
        this.http
            .get('assets/libsearch.json')
            .pipe(map((libsearch: LibSearchJson) => {
                let libraries = libsearch.libraries;
                let books = libsearch.catalogue.books.map(jsonBook => {
                    let jsonAvails: any[] = jsonBook.availabilities;
                    let avails: Availability[] = jsonAvails.map<Availability>(avail => {
                        let date: Date = null;
                        if(avail.return_date){
                            date = new Date(avail.return_date)
                        }
                        return {
                            is_available: avail.is_available,
                            branch: avail.branch,
                            library: avail.library,
                            status: avail.status,
                            subloc: avail.subloc,
                            publication: avail.publication,
                            zizo_name: avail.zizo_name,
                            zizo_subname: avail.zizo_subname,
                            zizo_image_url: avail.zizo_image_url,
                            shelfmark: avail.shelfmark,
                            link: avail.link,
                            return_date: date
                        }
                    })
        
                    return {
                        author: jsonBook.author,
                        title: jsonBook.title,
                        goodreads_id: jsonBook.goodreads_id,
                        library_page: jsonBook.library_page,
                        cover_url: jsonBook.cover_url,
                        status: jsonBook.status,
                        goodreads_page: jsonBook.goodreads_page,
                        isbn: jsonBook.isbn,
                        pages: jsonBook.pages,
                        availabilities: avails,
                        'format': jsonBook.format,
                        cloudlibrary_id: jsonBook.cloudlibrary_id
                    }
                });

                return {
                    libraries,
                    catalogue: {
                        books
                    }
                }
            })).subscribe(libsearch => {
                this.libraries.next(libsearch.libraries);
                this.books.next(libsearch.catalogue.books);
            })
    }
}

interface LibSearchJson {
    libraries: Library[],
    catalogue: {
        books: Book[]
    }
}